    <div id="container"></div>
    <div @click="hideDialog" v-if="!isClick" id="dialog">
      <img src="static/image/dialog.png">
    </div>
    <div @click="divide" v-if="!isDivided&&dividable" id="divide">
      <img src="static/image/divide.png">
    </div>
    <div @click="showFirst" v-if="isDivided" class="floorBtn" id="ff">
      <img src="static/image/1f.png">
    </div>
    <div @click="showSecond" v-if="isDivided" class="floorBtn">
      <img src="static/image/2f.png">
    </div>
    <div @click="back" v-if="canReturn" id="back">
      <img src="static/image/back.png">
    </div>
    <div id="firstFloorBtn" v-if="firstIsShow">
      <img src="static/image/zt.png" @click="jump">
      <img src="static/image/sf.png">
      <img src="static/image/cls.png">
      <img src="static/image/hds.png">
      <img src="static/image/ct.png">
    </div>
  </div>
