  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      controls: null,
      selectedObject: null,
      group: null,
      mixer: null,
      object: null,
      isClick: false,
      isDivided: false,
      canReturn: false,
      //status: 0房屋整体模型未被分开时 1房屋模型被分为两层时 2选中单层模型时
