      this.isClick = true
    },
    divide(){
      let house, firstFloor, secondFloor, wall
      //获取房屋模型
      for(let i = 0; i < this.scene.children.length; i++) {
        if (this.scene.children[i].name === 'house'){
          house = this.scene.children[i]
        }
      }
      //获取分层展开的模型
      for(let i = 0; i < house.children.length; i++){
        // console.log(house.children[i].name)
        switch (house.children[i].name){
          case 'wall':
            this.wall = house.children[i]
            break
          case 'firstfloor2':
            this.firstFloor = house.children[i]
            break
          case 'second_floor':
            this.secondFloor = house.children[i]
            break
        }
      }

      // console.log(wall.position)
      // console.log(firstFloor.position)
      // console.log(secondFloor.position)


      //将对应模型移动位置
      this.wall.position.set(0, 100, 0)
      this.firstFloor.position.set(-7, 0, 4)
      this.secondFloor.position.set(7, -3, 4)

      //状态：1从整体刚刚分开到两部分  2选择一层楼之后又返回两层楼的状态

      //分开后显示两层楼的指示标签
      this.initLabel()
      //修改相应状态
      this.isDivided = true
      this.canReturn = true
      this.status = 1
      this.dividable = false
    },
    init() {
      let container = document.getElementById("container");
      this.camera = new Three.PerspectiveCamera(
        35,
        container.clientWidth / container.clientHeight,
        0.01,
        10000
      );
      // this.camera.position.z = 0.0
      this.camera.position.set(25, 0, 0)
      // console.log(this.camera)

      this.scene = new Three.Scene();
      this.scene.background = new Three.Color( 0xf6f0eb )
      // this.scene.background.color = new Three.Color(255, 255, 255)

      this.renderer = new Three.WebGLRenderer({ antialias: true });
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      container.appendChild(this.renderer.domElement);

      this.controls = new OrbitControls(this.camera, this.renderer.domElement)
      this.controls.minPolarAngle = Math.PI / 6
      this.controls.maxPolarAngle = Math.PI / 2

      // document.addEventListener('pointermove', this.onPointerMove)
      document.addEventListener('click', this.onMouseClick)

    },
    initLight(){
      const ambient = new Three.AmbientLight(0xffffff, 0.8)
      this.scene.add(ambient)

      const direction1 = new Three.DirectionalLight(0xdddabc, 0.4)
      direction1.position.set(0, 7, 10)
      direction1.castShadow = true
      this.scene.add(direction1)
      const dHelper1 = new Three.DirectionalLightHelper(direction1, 5)
      // this.scene.add(dHelper1)

      const direction2 = new Three.DirectionalLight(0xdddabc, 0.5)
      direction2.position.set(0, 2, -12)
      direction2.castShadow = true
      // this.scene.add(direction2)
      const dHelper2 = new Three.DirectionalLightHelper(direction2, 5)
      // this.scene.add(dHelper2)

      const direction3 = new Three.DirectionalLight(0xdddabc, 0.5)
      direction3.position.set(6, 2, 0)
      direction3.castShadow = true
      // this.scene.add(direction3)
      const dHelper3 = new Three.DirectionalLightHelper(direction2, 5)
      // this.scene.add(dHelper3)

      const direction4 = new Three.DirectionalLight(0xdddabc, 0.5)
      direction4.position.set(-6, 2, 0)
      direction4.castShadow = true
      // this.scene.add(direction4)
      const dHelper4 = new Three.DirectionalLightHelper(direction4, 5)
      // this.scene.add(dHelper4)

      const hemiLight = new Three.HemisphereLight( 0xffffff, 0x444444 );
      hemiLight.position.set( 0, 8, 0 );
      this.scene.add( hemiLight );
      const hHelper = new Three.HemisphereLightHelper(hemiLight, 12)
      // this.scene.add(hHelper)

      // const spot = new Three.SpotLight(0x404040, 0.7)
      // spot.target = this.object
      // spot.position.set(0, 20, 10)
      // const sHelper = new Three.SpotLightHelper(spot)
      // this.scene.add(spot)
      // this.scene.add(sHelper)


      // const light = new Three.AmbientLight( 0x404040 ); // soft white light
      // this.scene.add( light );
    },
    initRoom(){
      const cube = new Three.BoxGeometry(1000, 1000, -1000)
      let material = []

      const textureLoader = new Three.TextureLoader()
      const left = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/right.png')
      material.push(new Three.MeshBasicMaterial({map: left}))
      const right = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/left.png')
      material.push(new Three.MeshBasicMaterial({map: right}))

      const top = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/top.png')
      material.push(new Three.MeshBasicMaterial({map: top}))
      const bottom = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/bottom.png')
      material.push(new Three.MeshBasicMaterial({map: bottom}))
      const front = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/back.png')
      material.push(new Three.MeshBasicMaterial({map: front}))
      const back = textureLoader.load('https://fzkmuseum.cn/images/yyt/test/front.png')
      material.push(new Three.MeshBasicMaterial({map: back}))

      const room = new Three.Mesh(cube, material)
      this.scene.add(room)
    },
    initModel() {
      const _this = this
      const loader = new FBXLoader()
      loader.load('./static/model/entirety.fbx', object => {
        _this.object = object
        _this.scene.add(object)
        object.name = 'house'
        object.position.set(8, -2, 0)
        // console.log(_this.scene.children[5].children)
      })
    },
    // initLabel(){
    //   const texture = new Three.TextureLoader().load('https://fzkmuseum.cn/images/production/comic_static.png')
    //   const material = new Three.SpriteMaterial({ map: texture})
    //
    //   const sprite = new Three.Sprite(material)
    //   sprite.name = 'entry'
    //   sprite.position.set(10, -3, 0)
    //
    //   // const geometry = new Three.BoxGeometry(1, 1, 0.1)
    //   // const box = new Three.Mesh(geometry, material)
    //   // box.position.set(10, -3, 0)
    //
    //   this.group = new Three.Group()
    //   this.group.add(sprite)
    //   // this.group.add(box)
    //
    //   this.scene.add(this.group)
    // },
    initHelper(){
      const helper = new Three.AxesHelper(20)
      helper.position.set(0, 0, 0)
      //red -> x  green -> y  blue -> z
      helper.setColors(new Three.Color(255, 0, 0), new Three.Color(0, 255,0), new Three.Color(0, 0,255))
      // this.scene.add(helper)
    },
    // onMouseClick( event ){
