    //   pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1
    //   pointer.y = ( event.clientY / window.innerHeight ) * 2 + 1
    //
    //   rayCaster.setFromCamera( pointer, this.camera )
    //
    //   const intersects = rayCaster.intersectObjects(house)
    //   console.log(intersects)
    //   for (let i = 0; i < intersects.length; i++){
    //     console.log(intersects[i])
    //   }
    //
    //
    //   // console.log(this.scene.children)
    //   // const intersects = raycaster.intersectObjects(this.scene.children)
    //   // console.log(this.scene.children)
    //   // console.log(intersects)
    //   // for (let i = 0; i < intersects.length; i++){
    //   //   console.log(intersects[i])
    //   //   if (intersects[i].name === 'house'){
    //   //     console.log('click house')
    //   //   }
    //   // }
    //
    //   // const intersects = raycaster.intersectObjects(this.group.children)
    //   // console.log(this.group.children)
    //   // if (intersects.length !== 0){
    //   //   console.log(intersects)
    //   // }
    //
    //   // 0720 click test
    //   // const group = this.scene.children[5]
