    //   //       break
    //   //   }
    //   // }
    // //
    // //   wall.position.set(0, 50, 0)
    // //   firstFloor.position.set(0, -4, 4)
    // //   secondFloor.position.set(-18, 1, 4)
    //   //点击分层 end
    //
    // },
    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
    },
    initLabel(){
      //点击分层之后，有两层楼并列显示的状态下显示
      //判断是否是该状态
      const image1 = new Three.TextureLoader().load('../../static/image/firstFloorLabel.png')
      const image2 = new Three.TextureLoader().load('../../static/image/secondFloorLabel.png')

      const material1 = new Three.SpriteMaterial( {map: image1})
      const material2 = new Three.SpriteMaterial( {map: image2})

      this.firstFloorLabel = new Three.Sprite(material1)
      this.secondFloorLabel = new Three.Sprite(material2)

      this.firstFloorLabel.scale.set(1,3.5,0)
      this.firstFloorLabel.position.set(-12, 4, 0)

      this.secondFloorLabel.scale.set(1, 4, 0)
      this.secondFloorLabel.position.set(12, 4, 0)

      this.scene.add(this.firstFloorLabel)
      this.scene.add(this.secondFloorLabel)

    },
    hideLabel(){
      this.firstFloorLabel.visible = false
      this.secondFloorLabel.visible = false
    },
    showFirst(){
      this.firstFloor.position.set(-2, 0, 5)
      this.secondFloor.position.set(0, 1000, 0)
      this.hideLabel()
      this.isDivided = false
      this.status = 2
      this.firstIsShow = true
    },
    showSecond(){
      this.firstFloor.position.set(0, 1000, 0)
      this.secondFloor.position.set(0, -2, 5)
      this.hideLabel()
      this.isDivided = false
      this.status = 2
      this.secondIsShow = true
    },
    back(){
      switch (this.status){
        case 1:{
          this.wall.position.set(0, 0, 0)
          this.firstFloor.position.set(0, 0, 0)
          this.secondFloor.position.set(0, 0, 0)
          //修改相关状态
          this.isDivided = false
          this.canReturn = false
          this.dividable = true
          this.hideLabel()
          this.status = 0
          break
        }
        case 2:{}
          this.firstFloor.position.set(-7, 0, 4)
          this.secondFloor.position.set(7, -3, 4)
          this.initLabel()
          this.isDivided = true
          this.status = 1
          this.firstIsShow = false
          this.secondIsShow = false
      }
    },
    jump(){
      this.$router.push('/visit')
    }
  },
  mounted() {
    this.init()
    this.initLight()
    // this.initRoom()
    this.initModel()
    // this.initLabel()
    this.initHelper()
    this.animate()
  },
};

</script>

<style scoped>
#container {
  height: 100vh;
  width: 100vw;
  z-index: -100;
}

#dialog{
  width: 100vw;
  height: 100vh;
  z-index: 100;
  position: fixed;
  top: 0;
}

#dialog > img{
  position: fixed;
  bottom: 0 ;
  display: block;
  width: 100vw;
  margin: 0 auto;
}

#divide{
  width: 12vw;
  position: fixed;
  bottom: 5vh;
  right: 3vw;
  z-index: 50;
}

#divide > img{
  width: 12vw;
}

.floorBtn{
  width: 12vw;
  position: fixed;
  z-index: 50;
  right: 3vw;
  bottom: 5vh;
}

.floorBtn:first-of-type{
  /*right: 13vw;*/
  /*position: relative;*/
  position: fixed;
  right: 15vw;
  bottom: 10vh;
}

.floorBtn > img{
  width: 12vw;
}

#ff{
  position: fixed;
  right: 18vw;
}

#back{
  position: fixed;
  top: 8vh;
  left: 4vw;
}

#back > img{
  width: 2.8vw;
}

#firstFloorBtn{
  position: fixed;
  overflow: scroll;
  top: 20vh;
  right: 4vw;
  writing-mode: vertical-lr;
}

#firstFloorBtn > img{
  width: 12vw;
  margin-bottom: 1vh;
}
</style>



