<template>
  <div>
    <div id="container">

    </div>
    <div @click="hidden" id="moreInfo" :style="{display: infoIsShow}">
      <img src="static/image/info.png">
    </div>
  </div>
</template>

<script>
import * as Three from "three";
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls";

export default {
  name: "visit",
  data(){
    return{
      scene: null,
      label: null,
      group: null,
      infoIsShow: 'none'
    }
  },
  methods:{
    init() {
      let container = document.getElementById("container");
      this.camera = new Three.PerspectiveCamera(
        45,
        container.clientWidth / container.clientHeight,
        0.5,
        10000
      );

      // this.camera.position.z = 0.0
      this.camera.position.set(0, 0.5, 0)
      // this.camera.lookAt(8, 2, 8)
      // console.log(this.camera)

      this.scene = new Three.Scene();
      this.scene.background = new Three.Color( 0xffffff )
      // this.scene.background.color = new Three.Color(255, 255, 255)

      this.renderer = new Three.WebGLRenderer({ antialias: true });
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      container.appendChild(this.renderer.domElement);

      this.controls = new OrbitControls(this.camera, this.renderer.domElement)
      this.controls.minPolarAngle = Math.PI / 6
      this.controls.maxPolarAngle = Math.PI / 2

      // document.addEventListener('pointermove', this.onPointerMove)
      document.addEventListener('click', this.onMouseClick)
      document.addEventListener( 'pointermove', this.onPointerMove );

    },
    initRoom(){
      const cube = new Three.BoxGeometry(10, 10, -10)
      let material = []

      const textureLoader = new Three.TextureLoader()
      const left = textureLoader.load('../../static/image/yyt/test/right.png')
      material.push(new Three.MeshBasicMaterial({map: left}))
      const right = textureLoader.load('../../static/image/yyt/test/left.png')
      material.push(new Three.MeshBasicMaterial({map: right}))
      const top = textureLoader.load('../../static/image/yyt/test/top.png')
      material.push(new Three.MeshBasicMaterial({map: top}))
      const bottom = textureLoader.load('../../static/image/yyt/test/bottom.png')
      material.push(new Three.MeshBasicMaterial({map: bottom}))
      const front = textureLoader.load('../../static/image/yyt/test/back.png')
      material.push(new Three.MeshBasicMaterial({map: front}))
      const back = textureLoader.load('../../static/image/yyt/test/front.png')
      material.push(new Three.MeshBasicMaterial({map: back}))

      // const material1 = new Three.MeshBasicMaterial( { color: 0x00ff00 } )

      const room = new Three.Mesh(cube, material)
      room.name = 'room'
      room.position.set(0,0,0)
      this.scene.add(room)
    },
    initLabel(){
      const label = new Three.TextureLoader().load('../../static/image/showMore.png')
      const material = new Three.SpriteMaterial( { map: label })


      // const geometry1 = new Three.BoxGeometry( 1, 1, 1 );
      // const cube = new Three.Mesh( geometry1, material );
      // this.scene.add( cube );

      // const geometry = new Three.CircleGeometry( 1, 32 );
      // const circle = new Three.Mesh( geometry, material );
      // this.scene.add( circle );

      // const geometry = new Three.CircleGeometry( 0.3, 32 );
      // const material = new Three.MeshBasicMaterial( { map: label } );
      // const circle = new Three.Mesh( geometry, material );
      // circle.position.set(0.6, -0.5, -3)
      // circle.name = 'show'
      // this.group = new Three.Group()
      // this.group.add(circle)
      // this.scene.add( circle );

      this.label = new Three.Sprite(material)
      this.label.name = 'show'
      this.label.position.set(-0.3, -0.35, -1.7)
      this.label.scale.set(0.4, 0.4, 0.4)
      this.group = new Three.Group()
      this.scene.add( this.group )
      this.group.add(this.label)
      // const a = new Three.BoxGeometry( 1, 1, 1 );
      // const cube = new Three.Mesh( a, material );
      // cube.scale.set(0.7, 0.7, 0.7)
      // cube.name = 'show'

      // this.group = new Three.Group()
      // this.group.add(cube)
      // this.scene.add(this.group)
      //
      //
      // const geometry = new Three.BoxGeometry( 1, 1, 0.1 );
      // const material1 = new Three.MeshBasicMaterial( { color: 0xbbffaa } );
      // const sphere = new Three.Mesh( geometry, material1 );
      // sphere.name = 'show'
      // sphere.position.set(0, -0.8, -4)
      //
      // this.scene.add( sphere );
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
    },
    onMouseClick( event ){
      const rayCaster = new Three.Raycaster()
      const pointer = new Three.Vector2()

      // const mouseX = (event.clientX / canvas.width) * 2 - 1;
      // const mouseY = - (event.clientY / canvas.height) * 2 + 1;

      pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1
      pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1

      rayCaster.setFromCamera( pointer, this.camera )

      const intersects = rayCaster.intersectObject(this.group, true)
      // console.log(intersects)
      if ( intersects.length > 0 ) {
        const res = intersects.filter( function ( res ) {
          return res && res.object;
        } )[ 0 ];
        if ( res && res.object ) {
          this.infoIsShow = 'block'
        }
      }
    },
    onPointerMove( event ){
      const rayCaster = new Three.Raycaster()
      const pointer = new Three.Vector2()

      // const mouseX = (event.clientX / canvas.width) * 2 - 1;
      // const mouseY = - (event.clientY / canvas.height) * 2 + 1;

      pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1
      pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1

      rayCaster.setFromCamera( pointer, this.camera )

      const intersects = rayCaster.intersectObjects(this.group, true)
    },
    hidden(){
      this.infoIsShow = 'none'
    }
  },
  mounted() {
    this.init()
    this.initLabel()
    this.initRoom()
    this.animate()
  }
}
</script>

<style scoped>
#container{
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

#moreInfo{
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 100;
  overflow: hidden;
}

#moreInfo > img{
  display: block;
  width: 35vw;
  margin: 0 30vw;
  /*margin*/
  /*margin: 10vh auto;*/
}
</style>



