<template>
  <div id="body">
    <div id="top" :class="topClass">
      <div class="scroll left"></div>
      <div class="scroll right"></div>
      <img src="../../static/image/pic1.png" :class="pic1" class="background p1">
      <img src="../../static/image/pic2.png" :class="pic2" class="background p2">
      <img src="../../static/image/pic3.png" :class="pic3" class="background p3">
      <img src="../../static/image/loading.png" :class="loadClass" class="btn">
      <img @click="hideLoading()" src="../../static/image/start.png" :class="startClass" class="btn">
    </div>
    <div id="bottom" :style="{ 'z-index': bottomIndex }">
      <home v-if="ready"></home>
    </div>
  </div>
</template>

<script>
export default {
  name: "loading",
  data(){
    return {
      pic1: '',
      pic2: '',
      pic3: '',
      btnUrl: '../../static/image/loading.png',
      ready: false,
      startClass: '',
      loadClass: '',
      topClass: '',
      bottomIndex: -1000
    }
  },
  mounted() {
    // this.changePicture()
    this.loadModel()
    this.changeIcon()

    // 测试用，没有加载页面
    // this.hideLoading()
  },
  methods: {
    changePicture() {
      let i = 0
      setTimeout(() => {
        setInterval(() => {
          i===3?i=0:i
          if(i === 0){
            this.pic1 = 'out'
            this.pic2 = 'in'
          }else if(i === 1){
            this.pic2 = 'out'
            this.pic3 = 'in'
          }else{
            this.pic3 = 'out'
            this.pic1 = 'in'
          }
          i++
        }, 4500)
      }, 3000)
    },
    loadModel(){
      setTimeout(() => {
        this.ready = true
        const home = () => import('@/components/home.vue')
      }, 3500)

    },
    changeIcon(){
      setTimeout(() => {
        this.loadClass = 'load'
        this.startClass = 'start'
      }, 7000)
    },
    hideLoading(){
      this.topClass = 'hidden'
      this.bottomIndex = 100
    }
  },
}
</script>

<style scoped>
#top{
  height: 100vh;
  width: 100vw;
  background-color: #efeae4;
  z-index: 10;
}

#bottom{
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
}

.scroll{
  width: 2.5vw;
  height: 82vh;
  display: inline-block;
  position: fixed;
  top: 6vh;
  z-index: 100;
}

.left{
  background: url('../../static/image/left.png');
  background-size: cover;
  left: 48vw;
  animation: toLeft 4s;
  animation-fill-mode: forwards;
}

.right{
  background: url('../../static/image/right.png');
  background-size: cover;
  right: 48vw;
  animation: toRight 4s;
  animation-fill-mode: forwards;
}

.background{
  animation: fill 4s;
  animation-fill-mode: forwards;
  width: 76vw;
  height: 80vh;
  position: absolute;
  top: 6.5vh;
  left: 12vw;
  object-fit: cover;
}

.p1{
  z-index: 3;
}

.p2{
  z-index: -10;
}

.p3{
  z-index: -10;
}

@keyframes toLeft {
  0%{
    left: 48vw;
  }
  100%{
    left: 12vw;
  }
}

@keyframes toRight {
  0%{
    right: 48vw;
  }
  100% {
    right: 12vw;
  }
}

@keyframes fill {
  0%{
    width: 0;
    left: 50vw;
    opacity: 0;
  }
  100%{
    width: 76vw;
    left: 12vw;
    opacity: 1;
  }
}

.btn{
  width: 10vw;
  position: fixed;
  bottom: 6vh;
  right: 0.8vw;
}

.btn:last-child{
  z-index: -5;
}

@keyframes hide {
  0%{
    opacity: 1;
    z-index: 10;
  }
  50%{
    z-index: 10;
  }
  80%{
    opacity: 0;
    z-index: 10;

  }
  100%{
    opacity: 0;
  }
}

.out{
  animation: hide 2s;
  animation-fill-mode: forwards;
}


@keyframes show {
  0%{
    opacity: 0;
  }
  10%{
    z-index: 10;
  }
  100%{
    z-index: 10;
    opacity: 1;
  }
}

.in{
  animation: show 2s;
  animation-fill-mode: forwards;
}

.start{
  animation: show 1.5s;
  animation-fill-mode: forwards;
}

.load{
  animation: hide 1.5s;
  animation-fill-mode: forwards;
}

@keyframes hideLoading{
  0%{
    opacity: 1;

  }
  80%{
    opacity: 0;
  }
  100%{
    z-index: -1000;
    opacity: 0;
  }
}

.hidden{
  animation: hideLoading 2.5s;
  animation-fill-mode: forwards;
}

</style>



